import {GameComponent} from "core/ui/components/games/gameComponent"
import {Task, Item} from "games/mnemonics/picturewords/logic"
import {Sizes} from "core/ui/common/sizes"
import {Btn, ImageBtn} from "core/ui/controls/btn"
import {RandomGenerator} from "core/utils"
import {Control, Txt} from 'core/ui/controls'
import {AnswerStatus, ILastAnswer} from "core/types"

import * as _ from 'lodash'

import {ItemComponent} from "games/mnemonics/picturewords/components/itemComponent"
import {AnswersComponent} from "games/mnemonics/picturewords/components/answersComponent"
import {Images, Translations} from "../assets";
import {CoreColors, CoreFonts} from "../../../../core/assets";
import {Tweens} from "../../../../core/ui/animations/tweens";
import {Img} from "../../../../core/ui/controls";
import {BtnDefaultImg} from "../../../../core/ui/controls/GraphicsImg";
import {Fonts} from "../../wordassociation/assets";
import {TutorialTextComponent} from "core/ui/components/games/gameUI/tutorialTextComponent";
import {BiPosXY, BiValue} from "core/ui/common/positions"

type PosData1 = {
  scale: boolean,
  biPos: BiPosXY,
  type: number,
  zIndex: number,
  sign: string
}

type PosData2 = {
  scale: boolean,
  zIndex: number,
  biPos: BiPosXY
}

const biValues = BiValue.create({
  data: {
    svetok_4Visible: true,
    lampa_1Visible: true
  }
}, {
  data: {
    svetok_4Visible: false,
    lampa_1Visible: false
  }
});

export class QuestionComponent extends GameComponent {
  rng = new RandomGenerator();

  positions = Sizes.create({
    screenWidth: 800,
    btnSize: {width: 500, height: 100},
    svetok_4: {x: 380, y: 720},
    lampa_1: {x: 400, y: 100}
  });

  biPositions = Sizes.bi({
    rememberBtn: {x: 0, y: 1140},
    answers: {x: 0, y: 1071},
    svetok_3: {x: -700, y: 0}
  }, {
    rememberBtn: {x: 0, y: 1310},
    answers: {x: 0, y: 1277},
    svetok_3: {x: -300, y: -55}
  });

  currentScreen: any

  screens: any = [
    {
      "1_0": {
        biPositions: Sizes.bi({
          pos: {x: -420, y: 510}
        }, {
          pos: {x: -250, y: 460}
        }),
        alterBiPos: Sizes.bi({
          pos: {x: 375, y: 510}
        }, {
          pos: {x: 230, y: 460}
        }),
        type: 1,
        alterZIndex: 1,
        portrait: {
          scale: false,
          zIndex: 1
        },
        landscape: {
          scale: false,
          zIndex: 1
        }
      },
      "1_1": {
        biPositions: Sizes.bi({
          pos: {x: 375, y: 510}
        }, {
          pos: {x: 285, y: 460}
        }),
        alterBiPos: Sizes.bi({
          pos: {x: -420, y: 510}
        }, {
          pos: {x: -190, y: 460}
        }),
        type: 1,
        alterZIndex: 2,
        portrait: {
          scale: false,
          zIndex: 2
        },
        landscape: {
          scale: false,
          zIndex: 2
        }
      },
      "2_0": {
        biPositions: Sizes.bi({
          pos: {x: -340, y: 795}
        }, {
          pos: {x: -180, y: 720}
        }),
        alterBiPos: Sizes.bi({
          pos: {x: 440, y: 780}
        }, {
          pos: {x: 220, y: 1020}
        }),
        type: 2,
        alterZIndex: 6, // красный стул
        portrait: {
          scale: true,
          zIndex: 3
        },
        landscape: {
          scale: true,
          zIndex: 3
        }
      },
      "2_1": {
        biPositions: Sizes.bi({
          pos: {x: 440, y: 780}
        }, {
          pos: {x: 220, y: 1020}
        }),
        alterBiPos: Sizes.bi({
          pos: {x: -340, y: 795}
        }, {
          pos: {x: -200, y: 720}
        }),
        type: 2,
        alterZIndex: 3, // синий стул
        portrait: {
          scale: true,
          zIndex: 6
        },
        landscape: {
          scale: true,
          zIndex: 4
        }
      },
      "3_0": {
        biPositions: Sizes.bi({
          pos: {x: -615, y: 865}
        }, {
          pos: {x: -250, y: 1050}
        }),
        alterBiPos: Sizes.bi({
          pos: {x: 40, y: 770}
        }, {
          pos: {x: 150, y: 700}
        }),
        type: 3,
        alterZIndex: 4, // без листьев цветок
        portrait: {
          scale: false,
          zIndex: 5
        },
        landscape: {
          scale: false,
          zIndex: 5
        }
      },
      "3_1": {
        biPositions: Sizes.bi({
          pos: {x: 40, y: 770}
        }, {
          pos: {x: 120, y: 700}
        }),
        alterBiPos: Sizes.bi({
          pos: {x: -615, y: 865}
        }, {
          pos: {x: -250, y: 1050}
        }),
        type: 3,
        alterZIndex: 5, // с листьями цветок
        portrait: {
          scale: false,
          zIndex: 4
        },
        landscape: {
          scale: false,
          zIndex: 6
        }
      }
    }
  ];

  items: {item: Item, posData: PosData2}[];
  itemComponents: ItemComponent[];
  answersGroup: AnswersComponent;

  svetok_3: Img;

  lampa_1 = this.makeImage(Images.lampa_1);
  svetok_4 = this.makeImage(Images.svetok_4);
  itemsPositions: PosData1[];

  tutorial: TutorialTextComponent;

  rememberBtn: Btn

  onAnswer(answer: ILastAnswer): void {
    const task = answer.levelTask.task as Task;

    if (answer.status == AnswerStatus.CorrectPartial) {
      const currentAnswer = answer.answerId;

      const currentActiveItem = this.itemComponents.filter(itemGroup => _.find(itemGroup.txtWords, txtWord => txtWord.text === currentAnswer))[0];

      const otherAnswers = currentActiveItem.txtWords.filter(txtWord => txtWord !== currentAnswer);
      const asnwersSeq = task.answers.seq;

      const wordsToShow = otherAnswers.filter(otherWord => asnwersSeq.indexOf(otherWord.text) < asnwersSeq.indexOf(currentAnswer))
        .map(otherAnswer => otherAnswer.text)
        .concat(currentAnswer);

      currentActiveItem.showSpecificWords(wordsToShow);
      currentActiveItem.hideHightLight();

      // answer.index - это индекс ответа (как правильного, так и не правильного)
      // answer.correctAnswerIndex - это индекс правильного ответа (то есть только правильного) - нужно в tutorial
      const nextQuestion = task.questions[answer.correctAnswerIndex! + 1];
      const nextImage = _.find(this.itemComponents, itemGroup => itemGroup.props.item.id === nextQuestion.itemId)!.bgImage

      this.itemComponents.filter(itemGroup => itemGroup.bgImage === nextImage)[0].showHighlight();

      this.answersGroup.createAnswers(nextQuestion.allAnswers);
    }
  }

  createItemComponents(items: {item: Item, posData: PosData2}[]) {
    if (!this.isFirstTask) {
      this.itemComponents.forEach(itemGroup => {
        itemGroup.removeAll(true);
      });
    }

    this.items = [];

    this.itemComponents = items.map(({item, posData}) => {
      return this.add(this.createComponentWithProps(ItemComponent, {item, scale: false})).setBiPos(posData.biPos)
    });
  }

  onRemember() {
    if (this.isTutorial) {
      this.tutorial.setProps(this.translate(Translations.tutor2));
    }

    this.disableInput(false);

    const task = this.getCurrentTask<Task>();

    if (task.changePositions) {
      this.itemComponents.forEach(itemGroup => {
        itemGroup.removeAll(true);
      });

      this.items = [];

      const orientation = this.isPortrait ? 'portrait': 'landscape';

      this.items = _.sortBy(task.items.map((item: Item) => {
        const currentItem = this.currentScreen[item.sign];

        const posData: PosData2 = {
          scale: currentItem[orientation].scale,
          zIndex: currentItem.alterZIndex,
          biPos: currentItem.alterBiPos.pos,
        }

        return {item, posData}
      }), x => x.posData.zIndex);

      this.itemComponents = this.items.map(({item, posData}) => {
        return this.add(this.createComponentWithProps(ItemComponent, {item, scale: posData.scale})).setBiPos(posData.biPos)
      });
    }

    this.itemComponents.forEach(itemGroup => itemGroup.hideAllWords());

    this.bringToTop(this.answersGroup);
    this.bringToTop(this.svetok_3);
    this.answersGroup.show();
    this.answersGroup.setBiPos(this.biPositions.answers);

    const currentImage = _.find(this.itemComponents, itemGroup => itemGroup.props.item.id === task.items[0].id)!.bgImage;

    this.itemComponents.filter(itemGroup => itemGroup.bgImage === currentImage)[0].showHighlight();
  }

  onNewTask(task: Task): void {
    if (this.isTutorial) {
      this.tutorial = this.add(this.createComponent(TutorialTextComponent) // создаем табличку
        .make({panel: {alpha: 0.6}})); // make - добавляет ей параметры
      this.tutorial.setProps(this.translate(Translations.tutor1)) // и перевод
    }

    this.disableInput(true);

    if (this.isFirstTask) {
      this.svetok_3 = this.addImage(Images.svetok_3).setBiPos(this.biPositions.svetok_3);
    } else {
      this.answersGroup.removeAll(true);
    }

    this.currentScreen = this.screens[task.bgId];

    this.items = _.sortBy(task.items.map((item: Item) => {
      const currentItem = this.currentScreen[item.sign];
      const orientation = this.isPortrait ? 'portrait': 'landscape';

      const posData: PosData2 = {
        scale: false,
        zIndex: currentItem[orientation].zIndex,
        biPos: currentItem.biPositions.pos,
      }

      return {item, posData}
    }), x => x.posData.zIndex);

    this.itemsPositions = task.items.map((item: Item) => {
      const currentItem = this.currentScreen[item.sign];
      const orientation = this.isPortrait ? 'portrait': 'landscape';

      return {
        biPos: currentItem.biPositions.pos,
        type: currentItem.type,
        scale: currentItem[orientation].scale,
        zIndex: currentItem[orientation].zIndex,
        sign: item.sign
      };
    });

    this.createItemComponents(this.items);

    if (this.isFirstTask) {
      this.add(this.lampa_1).setPos(this.positions.lampa_1);
      this.add(this.svetok_4).setPos(this.positions.svetok_4);

      this.rememberBtn = this.add(new Btn(this.translate(Translations.remember), () => {
        this.rememberBtn.visible = false;

        this.onRemember();
      }, CoreFonts.button(CoreColors.black), new BtnDefaultImg({
        color: Fonts.yellow,
        size: this.positions.btnSize
      })))
        .anchorCenter()
        .setBiPos(this.biPositions.rememberBtn);
    }

    this.answersGroup = this.add(this.createComponent(AnswersComponent)).anchorCenter();

    this.answersGroup.createAnswers(task.questions[0].allAnswers)
    this.answersGroup.hide();

    if (!this.isFirstTask) {
      this.rememberBtn.visible = true;
    }
  }

  onInit() {
    this.onOrientationChange(() => {
      if (this.items) {
        this.createItemComponents(this.items);
      }
    });

    this.onBiValueChange(biValues.data, (control, data) => {
      this.lampa_1.visible = data.lampa_1Visible;
      this.svetok_4.visible = data.svetok_4Visible;
    });
  }
}



