import {GameComponent} from "core/ui/components/games/gameComponent"
import {Txt} from "core/ui/controls/txt"
import {Images} from "games/mnemonics/picturewords/assets"
import {Sizes} from "core/ui/common/sizes"

import * as _ from 'lodash'
import {ControlUtl} from "core/ui/controls/controlUtl";
import {Fonts} from "../../picturewords/assets";

export class TextComponent extends GameComponent {
  positions = Sizes.create({
    offsetY: 35,
    offsetX: 20,
    bgImageWidth: 290,
    oneWord: {
      offsetY: 15
    },
    twoWords: {
      offsetY: 5
    }
  });

  bgImage = this.add(this.makeImage(Images.Button));

  txtWords: Txt[] = [];

  showWords(wordsToShow: string[]) {
    this.bgImage.visible = true;
    this.txtWords.forEach(txtWord => txtWord.visible = _.includes(wordsToShow, txtWord.text));
  }

  show() {
    this.bgImage.visible = true;
    this.txtWords.forEach(txtWord => txtWord.visible = true);
  }

  hide() {
    this.bgImage.visible = false;
    this.txtWords.forEach(txtWord => txtWord.visible = false);
  }

  createWords(words: string[]) {
    const
      isOneWord = words.length === 1,
      wordType = isOneWord ? Fonts.oneWord : Fonts.twoWords,
      offsetY = isOneWord ? this.positions.oneWord.offsetY : this.positions.twoWords.offsetY;

    this.txtWords = words.map((word, i) => {
      return this.add(ControlUtl.scaleToFit( // чтобы слово умещалось в заданную ширину
        new Txt(word, wordType).setPos([this.positions.offsetX, i * this.positions.offsetY + offsetY]),
        this.positions.bgImageWidth - this.positions.offsetX * 2
      ));
    });
  }

  onPropsUpdate() {
  }
}



