import {ImagesMetaData} from "core/types"
import {CoreColors, CoreFonts} from "core/assets/fonts";

export module Sounds {
  export const keys: string[] = []
}

export module Translations {
  export const remember = "remember"; // "Запомнил"
  export const tutor1 = "tutor1"; // "Запомните какие слова принадлежат каким предметам и нажмите на кнопку 'Запомнил'"
  export const tutor2 = "tutor2"; // "Выберите слово из списка, которое было у мигающего предмета"
}

export module Fonts {
  export const highlight = 0xECFDAC

  export const oneWord = {
    fontSize: 40, font: CoreFonts.MontserratSemiBold, fill: CoreColors.black, align: "center"
  }

  export const twoWords = {
    fontSize: 30, font: CoreFonts.MontserratSemiBold, fill: CoreColors.black, align: "center"
  }
}

export module Images {
  export const Button = "Button";
  export const button_outline = "button_outline";
  export const lampa_mob = "lampa_mob";
  export const kover = "kover";
  export const lampa_1 = "lampa_1";
  export const lampa_2 = "lampa_2";
  export const lampa_3 = "lampa_3";
  export const lampa_4 = "lampa_4";
  export const podloskka = "podloskka";
  export const pol = "pol";
  export const polka_3 = "polka_3";
  export const svetok_3 = "svetok_3";
  export const svetok_4 = "svetok_4";
  export const tapok = "tapok";

  export const bg = (id: number) => "bg_" + id;

  export const item = (sign: string, isOutlined: boolean = false) => {
    const outlined = isOutlined ? '_outline' : '';
    return `item_${sign}${outlined}`;
  };

  export const data: ImagesMetaData = {
    sprites: []
  }

}




