import {GameComponent} from "core/ui/components/games/gameComponent"
import {Txt} from "core/ui/controls/txt"
import {Item} from "games/mnemonics/picturewords/logic"
import {Images} from "games/mnemonics/picturewords/assets"
import {Sizes} from "core/ui/common/sizes"
import {RandomGenerator} from "core/utils"
import {Control, Img} from 'core/ui/controls'

import * as _ from 'lodash'
import {CommonAnimations} from "core/ui/animations/commonAnimations";
import {Btn} from "core/ui/controls/btn";
import {CoreColors, CoreFonts} from "../../../../core/assets";
import {ImageWithShadow} from "../../../../core/ui/controls/imgWithShadow";
import {screenSizeService} from "../../../../core/ui/services/instances";
import {ControlUtl} from "../../../../core/ui/controls/controlUtl";
import {BiValue} from "../../../../core/ui/common/positions";

const biValues = BiValue.create({
  scale: 1
}, {
  scale: 0.76
})

export class AnswersComponent extends GameComponent {
  rng = new RandomGenerator();

  biPositions = Sizes.bi({
    marginNumbers: {x: 30},
    btnsGroup: {x: 380, y: 60},
  }, {
    marginNumbers: {x: 20},
    btnsGroup: {x: 678, y: 45},
  });

  answers: Btn[] = [];

  bg = this.add(this.makeImage(Images.podloskka))
  btnsGroup = this.add(new Control()).setBiPos(this.biPositions.btnsGroup);

  hide() {
    this.visible = false;
  }

  show() {
    this.visible = true;
  }

  // changeBg() {
  //   const bgKey = this.isPortrait ? Images.podloskka : Images.podloskka;
  //
  //   this.bg.key = this.gameKey(bgKey) //.setPos([0, 200]); // key - это название спрайта
  //   // this.gameKey - добавляет приставку к названию картинки именем игры (был bg, стал pictureword-bg)
  //
  //   this.updatePivot();
  // }

  createAnswers(answers: string[]) {
    this.btnsGroup.removeAll(true);
    this.answers = [];

    this.answers = this.rng.derangement(answers.map((answer, i) => {
      const bgImage = new Img(this.gameKey(Images.Button));

      return this.btnsGroup.add(
        this.addAnswerBehavior(
          new Btn(
            answer,
            () => {},
            CoreFonts.button(CoreColors.black),
            bgImage,
            // чтобы слово умещалось в заданную ширину, а 20 - это чтобы был отступ по 10 слева и справа
            {maxTextWidth: bgImage.width - 40}
          ),
          answer,
          {dark: true, location: "top"}
        )
      );
    }));

    this.add(this.btnsGroup);

    const colCount = this.isPortrait ? 3 : 4;
    Sizes.tableFromList(this.answers, colCount, this.current(this.biPositions.marginNumbers));
    this.answersAreReady();
  }

  updateAnswersPositions() {
    this.btnsGroup.setBiPos(this.biPositions.btnsGroup);

    const colCount = this.isPortrait ? 3 : 4;
    Sizes.tableFromList(this.answers, colCount, this.current(this.biPositions.marginNumbers))
  }

  onPropsUpdate() {
  }

  onInit() {
    this.onOrientationChange(() => {
      // this.changeBg();
      this.updateAnswersPositions();
    });

    this.onBiValueChange(biValues.scale, (c, v) => {
      c.btnsGroup.symmetricScale = v
    })
  }
}



