import {GameComponent} from "core/ui/components/games/gameComponent"
import {Txt} from "core/ui/controls/txt"
import {Item} from "games/mnemonics/picturewords/logic"
import {Images} from "games/mnemonics/picturewords/assets"
import {Sizes} from "core/ui/common/sizes"
import {RandomGenerator} from "core/utils"
import {Btn} from "core/ui/controls/btn";
import {Img} from 'core/ui/controls'

import * as _ from 'lodash'
import {CommonAnimations} from "core/ui/animations/commonAnimations";
import {ImageWithShadow} from "../../../../core/ui/controls/imgWithShadow";
import {CoreColors, CoreFonts} from "../../../../core/assets";
import {ControlUtl} from "../../../../core/ui/controls/controlUtl";
import {Control} from "../../../../core/ui/controls";
import {Sys} from "../../../../core/logic/sys";
import debug = Sys.debug;
import {Fonts} from "../../picturewords/assets";
import {tuple} from "../../../../core/utils/tuples";
import {TextComponent} from "./textComponent";
import {BiValue} from "core/ui/common/positions"

export class ItemComponent extends GameComponent<{ item: Item, scale: boolean }> {
  positions = Sizes.create({
    offsetY: 30,
    offsetX: 20,
    oneWord: {
      offsetY: 15
    },
    twoWords: {
      offsetY: 5
    }
  });

  biPositions = Sizes.bi({
    "1_0": {x: -145, y: -105},
    "1_1": {x: -140, y: -150},
    "2_0": {x: -150, y: 0},
    "2_1": {x: -145, y: -20},
    "3_0": {x: -135, y: -225},
    "3_1": {x: -140, y: -180},
  } as Record<string, {x: number, y: number}>, {
    "1_0": {x: -125, y: -105},
    "1_1": {x: -182, y: -220},
    "2_0": {x: -170, y: -60},
    "2_1": {x: -165, y: -20},
    "3_0": {x: -135, y: -180},
    "3_1": {x: -140, y: -90}
  });

  bgImage: Img;
  outline: Img;

  txtWords: Txt[] = [];

  wordsGroup: TextComponent;

  hideHightLight() {
    this.outline.visible = false;
  }

  showHighlight() {
    this.outline.visible = true;
  }

  addHightLight() {
    this.outline = this.addImage(Images.item(this.props.item.sign, true)).anchorCenter();
  }

  // // changePositions(newPosition: any) {
  // changePositions(biPos: any, scale: boolean) {
  //   // if (newPosition.scale) {
  //   if (scale) {
  //     this.bgImage.scale.x = -1;
  //     this.outline.scale.x = -1;
  //   }
  //
  //   // console.log(77, newPosition)
  //
  //   // this.setBiPos(newPosition.biPos)
  //   this.setBiPos(biPos)
  // }

  showSpecificWords(wordsToShow: string[]) {
    this.wordsGroup.showWords(wordsToShow)
  }

  hideAllWords() {
    this.wordsGroup.hide();
  }

  onPropsUpdate() {
    this.tag = {
      type: this.props.item.type,
      index: this.props.item.index,
      sign: this.props.item.sign
    };

    this.bgImage = this.add(this.makeImage(Images.item(this.props.item.sign))).anchorCenter();

    this.addHightLight();
    this.hideHightLight();

    if (this.props.scale) {
      this.bgImage.scale.x = -1;
      this.outline.scale.x = -1;
    }

    this.wordsGroup = this.add(this.createComponent(TextComponent))
      .setBiPos(this.biPositions[this.props.item.sign]);

    this.wordsGroup.createWords(this.props.item.words);

    this.txtWords = this.wordsGroup.txtWords;

    // this.wordsGroup.showDebug(); // чтобы посмотреть границы группы
  }
}



