import {GameLogic} from "core/logic"
import {Sys} from "core/logic/sys"
import {RandomGenerator} from "core/utils"
import {Level} from "./levels"
import * as _ from 'lodash'
import {UniqSeq} from "core/utils/circleSeq"
import {tuple} from "core/utils/tuples"
import {MnemonicsWords} from "../common/mnemonicsWords";

export type Item = {
  id: number
  words: string[]
  type: number
  index: number,
  sign: string
}

type Question = {
  itemId: number
  correctAnswer: string
  allAnswers: string[]
}

const bgCount = [1, 2, 3, 4];
const allItemIds = _.range(6);

export type Task = {
  bgId: number
  questions: Question[]
  items: Item[]
  answers: {seq: string[]}
  changePositions: boolean
}

export class Logic extends GameLogic<Task, Level> {
  rng = new RandomGenerator();

  words = MnemonicsWords.getWords();
  wordsSeq = new UniqSeq(0);

  chunkUnzip<T>(array: T[], count: number): T[]{
    return _.flatten(_.unzip(_.chunk(array, count)))
  }

  createQuestions(level: Level) {
    const words = this.words[level.wordDifficulty];

    const bgGroups = [ // количество групп на каждом экране
      [ // всего три типа для первого bg. У каждого типа по 2 места, куда могут быть расположены картинки этого типа
        {
          type: 1,
          index: _.range(2)
        },
        {
          type: 2,
          index: _.range(2)
        },
        {
          type: 3,
          index: _.range(2)
        }
      ]
    ];

    const bgId = 0 // this.rng.sample(this.bgGroups);

    let itemGroups = bgGroups[bgId];

    const items = this.rng.subset(allItemIds, level.elementsCount).map(id => {
      let randomItem = this.rng.sample(itemGroups);

      if (randomItem.index.length === 0) {
        itemGroups = itemGroups.filter(itemGroup => itemGroup.index.length);
        randomItem = this.rng.sample(itemGroups)
      }

      const index = randomItem.index.shift()!;

      return {
        id,
        words: this.wordsSeq.nextSubsetFrom(words, level.wordsCount),
        type: randomItem.type,
        index,
        sign: `${randomItem.type}_${index}`
      }
    });

    const allOneWordsFromEachItem = _.flatMap(items, x => this.rng.sample(x.words));

    const questionItems = this.chunkUnzip(
      _.flatMap(items, item => item.words.map(word => tuple(item, word))), level.wordsCount
    );

    const usedWords = _.range(questionItems.length).map(i => _.take(questionItems, i).map(x => x[1]));

    const questions = questionItems.map(([item, word], i) => {
      const fakeAnswersFromItems = _.difference(allOneWordsFromEachItem, item.words.concat(usedWords[i]));

      const fakeAnswers = this.wordsSeq.nextSubsetFrom(words, level.allAnswersCount - fakeAnswersFromItems.length - 1);

      const allAnswers = this.rng.shuffle([word, ...fakeAnswers, ...fakeAnswersFromItems]);

      const question: Question = {
        itemId: item.id,
        correctAnswer: word,
        allAnswers
      };

      return question;
    });

    return {bgId, items, questions};
  }

  nextTask(level: Level): Task {
    const {bgId, items, questions} = this.createQuestions(level);
    const answers = {seq: questions.map(x => x.correctAnswer)};
    const changePositions = level.changePositions;

    const task: Task = {
      bgId,
      questions,
      items,
      answers,
      changePositions
    };

    if (Sys.showLog) {
      console.log("task", task.answers.seq)
    }

    return task
  }

}



