import {Sizes} from "core/ui/common/sizes"
import {BaseMainGameComponent} from "core/ui/components/games/baseMainGameComponent"
import {OnInit} from "core/ui/components/onInit"
import {Images} from "games/mnemonics/picturewords/assets"
import {QuestionComponent} from 'games/mnemonics/picturewords/components/questionComponent'
import {ItemComponent} from "games/mnemonics/picturewords/components/itemComponent"
import {Task} from '../logic'
import {Translations} from "games/mnemonics/picturewords/assets"
import {TutorialTextComponent} from "core/ui/components/games/gameUI/tutorialTextComponent"
import {screenSizeService} from "core/ui/services/instances"
import {Numbers} from "core/utils/numbers"
import {Btn} from "core/ui/controls/btn"
import {CoreColors, CoreFonts} from "../../../../core/assets/fonts";
import {BtnDefaultImg} from "../../../../core/ui/controls/GraphicsImg";
import {Fonts} from "../../wordassociation/assets";
import {AnswersComponent} from "./answersComponent";
import {BiValue} from "../../../../core/ui/common/positions";

const durations = {}

const biValues = BiValue.create({
  data: {
    polkaVisible: true,
    koverVisible: true,
    lampa_2Visible: true,
    lampa_3Visible: true,
    lampa_4Visible: true,
    lampa_mobVisible: false,
    questionScale: 1
  }
}, {
  data: {
    polkaVisible: false,
    koverVisible: false,
    lampa_2Visible: false,
    lampa_3Visible: false,
    lampa_4Visible: false,
    lampa_mobVisible: true,
    questionScale: 0.9
  }
});

// const bg1 = {
//
// }

export class MainComponent extends BaseMainGameComponent implements OnInit {
  positions = Sizes.create({
    kover: {x: 150, y: 1080},
    lampa_2: {x: 1300, y: 0},
    lampa_3: {x: 430, y: 0},
    lampa_4: {x: 1130, y: 10},
    polka_3: {x: 890, y: -58},
    lampa_mob: {x: 665, y: 0}
  });

  biPositions = Sizes.bi({
    bg: {x: 0, y: 35},
    question: {x: Sizes.widthNormal / 2, y: 0},
    tapok: {x: 1250, y: 990},
    pol: {x: 1050, y: 773},
  }, {
    bg: {x: 0, y: -80},
    question: {x: Sizes.widthNormal / 2, y: 0},
    tapok: {x: 990, y: 1040},
    pol: {x: 850, y: 655},
  });

  bg = this.addImage(Images.bg(0)).setBiPos(this.biPositions.bg);
  kover = this.addImage(Images.kover).setPos(this.positions.kover);
  polka_3 = this.addImage(Images.polka_3).setPos(this.positions.polka_3);
  tapok = this.addImage(Images.tapok).setBiPos(this.biPositions.tapok);
  lampa_2 = this.addImage(Images.lampa_2).setPos(this.positions.lampa_2);
  lampa_3 = this.addImage(Images.lampa_3).setPos(this.positions.lampa_3);
  lampa_4 = this.addImage(Images.lampa_4).setPos(this.positions.lampa_4);
  pol = this.addImage(Images.pol).setBiPos(this.biPositions.pol);
  lampa_mob = this.addImage(Images.lampa_mob).setPos(this.positions.lampa_mob);

  question = this.add(this.createComponent(QuestionComponent))
    .setBiPos(this.biPositions.question);

  onInit(): void {
    this.onBiValueChange(biValues.data, (control, data) => {
      this.polka_3.visible = data.polkaVisible;
      this.kover.visible = data.koverVisible;
      this.lampa_2.visible = data.lampa_2Visible;
      this.lampa_3.visible = data.lampa_3Visible;
      this.lampa_4.visible = data.lampa_4Visible;
      this.lampa_mob.visible = data.lampa_mobVisible;

      this.question.symmetricScale = data.questionScale
    });

    this.startIntro(); // показывает верхнюю плашку с таймером, очками и левелами
  }

  onNewTask(task: Task) {
    // this.question.showDebug(); // чтобы посмотреть границы группы
  }
}




