import {createLevelsFromArray} from "core/types/logic"
import {MnemonicsWords} from "../common/mnemonicsWords";

export interface Level {
  elementsCount: number
  changePositions: boolean
  wordsCount: number
  wordDifficulty: MnemonicsWords.WordDifficulty
  allAnswersCount: number
}

function level(
  elementsCount: number,
  changePositions: boolean,
  wordsCount: number,
  wordDifficulty: MnemonicsWords.WordDifficulty
): Level {
  return {elementsCount, wordsCount, changePositions, wordDifficulty, allAnswersCount: 8}
}

export const levels = createLevelsFromArray([
  level(3, false, 1, "easy"),
  level(3, true, 1, "easy"),
  level(4, false, 1, "easy"),
  level(4, true, 1, "easy"),
  level(5, false, 1, "normal"),
  level(5, true, 1, "normal"),
  level(5, false, 2, "normal"),
  level(6, false, 1, "normal"),
  level(6, false, 2, "normal"),
  level(6, false, 2, "hard"),
  level(6, true, 2, "hard"),
  level(6, true, 2, "hard"),
  level(6, true, 2, "hard"),
])



